// API_PATH
import {
  API_PATH
} from '@/config'

// ajax
import ajax from '@/utils/ajax'

/**
 * 获取部门树状数据
 * @const
 * @type {String}
 * @default  '[API_PATH]/organization/tree'
 */
export const GET_ORG_TREE_DATA = API_PATH + '/organization/tree'

/**
 * 获取部门树状数据
 * @param {object} [data] 发送键值对数据
 * @returns {promise}
 */
export function getOrgTreeData(data) {
  return ajax({
    method: 'GET',
    headers: {
      "Content-Type": "application/x-www-form-urlencoded"
    },
    data: data,
    url: GET_ORG_TREE_DATA
  })
}
