<template>
  <div
    :class="$root.themeHomeChange === '1' ? 'project-manage-table_light' : 'project-manage-table'"
    ref="projectManageTableRef">
    <bda-scroll-table
      background
      :data="tableData"
      ref="table"
      popper-append-to-body
      border
      :blInitLoad="false"
      tooltip-effect="light"
      @select="handleSelectionChange"
      v-loading="tableLoading"
      element-loading-text="拼命加载中"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)"
    >
      <el-table-column
        type="index"
        label="序号"
        min-width="60"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="id"
        label="任务编号"
        min-width="160"
      >
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>{{ scope.row.id }}</p>
            <div slot="reference" class="name-wrapper">
              {{ scope.row.id }}
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column
        prop="taskName"
        label="任务名称"
        min-width="242"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="taskDesc"
        label="任务说明"
        min-width="270"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="taskType"
        label="任务类型"
        min-width="120"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          {{options.taskTypeOptions[scope.row.taskType]}}
        </template>
      </el-table-column>
      <el-table-column
        prop="process"
        label="业务流程"
        min-width="140"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="checkUnit"
        label="检查单位"
        min-width="140"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="timeQuantum"
        label="检查时间段"
        min-width="200"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          {{ scope.row.checkStartDate + '~' + scope.row.checkEndDate }}
        </template>
      </el-table-column>
      <el-table-column
        prop="timingTaskStartDate"
        label="定时任务开始时间"
        min-width="220"
        show-overflow-tooltip
      ></el-table-column>
      <!-- <el-table-column
        prop="taskPerformSync"
        label="执行周期"
        min-width="150"
        show-overflow-tooltip
      >
      </el-table-column> -->
      <el-table-column
        v-if="type === '我的审核'"
        prop="personnel"
        label="涉及人员"
        min-width="120"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="amountInvolved"
        label="涉及金额"
        min-width="140"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          {{scope.row.amountInvolved || '0' }}
        </template>
      </el-table-column>
      <el-table-column
        prop="regularOutliers"
        label="监测问题数"
        min-width="100"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          {{scope.row.regularOutliers || '0' }}
        </template>
      </el-table-column>
      <el-table-column
        prop="startDate"
        label="创建时间"
        min-width="120"
        show-overflow-tooltip
      ></el-table-column>
      <!-- <el-table-column
        prop="updateDate"
        label="更新时间"
        min-width="120"
        show-overflow-tooltip
      ></el-table-column> -->
      <el-table-column
        prop="taskStatus"
        label="任务状态"
        min-width="100"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <span >{{ options.taskStatus[scope.row.taskStatus] || '' }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="250" >
        <template slot-scope="scope">
          <el-button
            type="text"
            class="DetailBtn"
            style="padding-right: 10px;"
            @click="ruleDetails(scope.row)"
          >规则详情</el-button>
          <span v-if="type === '我的新增'">
            <el-button
              type="text"
              class="updateBtn"
              @click="editTask(scope.row)"
            >编辑</el-button>
            <el-button
              type="text"
              :disabled="!scope.row.taskStatus"
              class="stopBtn" @click="changeTaskStatus(scope.row)" >{{
                (scope.row.taskStatus == 2 || scope.row.taskStatus == 3 || scope.row.taskStatus == 5) ? '停用' : (scope.row.taskStatus == 1 || scope.row.taskStatus == 4) ? '启用' : '异常'
            }}</el-button>
            <el-button
              type="text"
              class="DeleteBtn"
              @click="deleteTaskItem(scope.row)"
            >删除</el-button>
          </span>
          <span v-else>
            <el-button
              type="text"
              class="DeleteBtn"
            >审核</el-button>
          </span>
        </template>
      </el-table-column>
    </bda-scroll-table>

    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pageInfoConfig.page"
      :page-sizes="[10, 20, 30, 40, 50]"
      :page-size="pageInfoConfig.rows"
      background
      layout="prev, pager, next, sizes, jumper, total"
      :total="pageInfoConfig.total"
    >
    </el-pagination>
  </div>
</template>
<script>
import Interface from "@/base/mixin/risk-control-center.js";
import qs from "qs";
export default {
  name: "projectManageTable",
  mixins: [Interface],
  data() {
    return {
      tableLoading: false,
      /*表格数据 */
      tableData: [],
      
      options: {
        taskTypeOptions: {
          '1': '实时性检查',
          '2': '计划性检查',
          '3': '专项性检查'
        },
        taskStatus: {
          '1': '已创建',
          '2': '进行中',
          '3': '已完成',
          '4': '已停用',
          '5': '已启用'
        },
        taskPerformSync: [
          { id: 1, value: 'one-day', name: "每天" },
          { id: 2, value: 'three-day', name: "每三天" },
          { id: 3, value: 'a-week', name: "每一周" },
          { id: 4, value: 'half-a-month', name: "每半个月" },
          { id: 5, value: 'a-month', name: "每一个月" },
          { id: 6, value: 'three-month', name: "每三个月" },
          { id: 7, value: 'half-a-year', name: "每半年" },
          { id: 8, value: 'a-month', name: "每一年" },
        ],
      },
      
      
      
      
      
      pageInfoConfig: {
        page: 1,
        rows: 20,
        total: 0
      }, //表格-分页初始配置
      tableHeight: "",
      selectData: [], //已选数据
    };
  },
  props: {
    params: {
      type: Object,
      default: () => {
        return {}
      }
    },
    type: {
      type: String,
      default: () => {
        return '我的新增'
      }
    }
  },
  watch: {
    // params: {
    //   handler(v) {
    //     window.console.log(v)
    //     this.emptyPages();
    //     this.initTable(v);
    //   },
    //   immediate: false,
    //   deep: true
    // }
  },
  computed: {},
  mounted() {},
  methods: {
    /**
     * @description:加载表格内容
     */
    initTable(params, isEmpty) {
      if(isEmpty) {
        this.setPages();
      }
      this.tableData = [];
      params.current = this.pageInfoConfig.page;
      params.size = this.pageInfoConfig.rows;
      this.tableLoading = true;
      this.getRiskTaskList(params).then((rs)=>{
        if(rs) {
          this.tableData = rs.list;
          this.setPages(rs.pageNum, rs.pageSize, rs.total);
        } else {
          this.setPages();
        }
      }).finally(()=>{
        this.tableLoading = false;
      })
    },
    setPages(page = 1, rows = 20, total = 0) {
      this.pageInfoConfig = {
        page,
        rows,
        total
      };
    },
    
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.table.toggleRowSelection(row);
        });
      } else {
        this.$refs.table.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.selectData = val;
    },
    handleSizeChange(val) {
      // window.console.log(`每页 ${val} 条`);
      this.pageInfoConfig.rows = val;
      this.initTable(this.params);
    },
    handleCurrentChange(val) {
      // window.console.log(`当前页: ${val}`);
      this.pageInfoConfig.page = val;
      this.initTable(this.params);
    },
    // 编辑合规任务
    editTask(row) {
      this.$emit('editTask', JSON.parse(JSON.stringify(row)));
    },
    
    // 改变合规任务状态
    changeTaskStatus(row) {
      let [title, status] = (row.taskStatus == 2 || row.taskStatus == 3 || row.taskStatus == 5) ?
          ['<strong style="color: #F56C6C;">停用</strong>', 0]
            :
          (row.taskStatus == 1 || row.taskStatus == 4) ?
            ['<strong style="color: #67C23A;">启用</strong>', 1] : ['* 错误 *', ''];
      this.$confirm(`此操作将 ${ title } 该合规任务, 是否继续?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        dangerouslyUseHTMLString: true
      }).then(() => {
        this.tableLoading = true;
        this.taskStatusChange({ taskId: row.id, status }).then(()=>{
          this.$message.success({
            dangerouslyUseHTMLString: true,
            message: '合规任务 ' + title + ' 成功！'
          })
        }).finally(()=>{
          this.initTable(this.params);
        })
      }).catch(() => {});
    },
    
    // 删除合规任务列表
    deleteTaskItem(row) {
      this.$confirm('此操作将删除该合规任务, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.tableLoading = true;
        this.deleteTaskById(qs.stringify({ taskId: row.id })).then(()=>{
          // window.console.log(rs);
          this.$message.success('合规任务删除成功！')
        }).finally(()=>{
          this.initTable(this.params);
        });
      }).catch(() => {});
    },
    
    ruleDetails(currentData) {
      window.console.log(currentData);
      this.$router.push({
        path: "/ruleList",
        query: {
          index: "6-1-3",
          taskId: currentData.id,
          actNav: this.$route.query.actNav,
        },
      });
    },
  },
};
</script>
<style lang="scss" scoped>
.project-manage-table {
  height: 100%;
  width: 100%;
  /deep/ {
    .button-container {
      margin-bottom: 10px;
    }

    /*修改表格单元格高度*/
    .bda-pagination-table {
      .bda-pagination-table-page {
        border: 0 !important;
        height: 50px !important;
      }
      .bda-pagination-table-main {
        height: calc(100% - 40px);
      }
    }

    /*checkbox样式*/

    .el-checkbox__input.is-disabled {
      width: 100%;
      padding: 0 auto;
    }

    .el-checkbox__input.is-disabled .el-checkbox__inner,
    .el-checkbox__input .el-checkbox__inner {
      background-color: #041338 !important;
      width: 16px;
      height: 16px;
      border: 1px solid #4389f9;
      border-radius: 2px;
    }

    /*解决调整单元格高度后，fixed错位*/
    .el-table__fixed-body-wrapper {
      top: 37px !important;
    }

    /*解决表格滚动高度固定导致的问题*/
    .el-table__body-wrapper.is-scrolling-left {
      height: calc(100% - 48px) !important;
    }
    .el-table__body-wrapper.is-scrolling-right {
      height: calc(100% - 48px) !important;
    }
    .el-table__body-wrapper.is-scrolling-middle {
      height: calc(100% - 48px) !important;
    }
    .el-table__fixed-body-wrapper {
      height: calc(100% - 34px) !important;
    }

    /*解决调整列宽度时，出现列内容未完整显示的问题*/
    .el-table .cell.el-tooltip {
      width: 100% !important;
    }

    .el-table__header-wrapper {
      background-color: #22519c;
    }

    .el-table__body-wrapper {
      background-color: #071a47;
    }

    .el-table__header th {
      background-color: #22519c !important;
      height: 48px;
      border-color: #000000;

      .cell {
        color: #fff !important;
        text-align: center;
      }
    }

    .bda-table-main {
      background-color: #071a47;
    }

    .bda-main {
      height: calc(100% - 52px);
      border: 1px solid #000000;
      .el-table__row {
        &:hover > td {
          background-color: #284385 !important;
        }
        > td {
          height: 48px;
          border-color: #000000;

          .cell {
            color: #fff;
            text-align: center;
            .el-button {
              color: #66b9ff;
            }
          }
        }
        > td:nth-last-child(3) {
          .cell {
            /*color: red;*/
            text-align: center;
          }
        }
      }
      .el-table__row:nth-of-type(odd) {
        background-color: #223a75;
      }
      .el-table__row:nth-of-type(even) {
        background-color: #1a3066;
      }
    }

    // 修改分页组件样式
    .el-pagination {
      margin-top: 20px;
      text-align: center;
      color: #fff;
      .btn-next,
      .btn-prev,
      .el-pager li,
      .el-select .el-input .el-input__inner,
      .el-pagination__editor.el-input .el-input__inner {
        background-color: #041338;
        color: #d3d3d3;
        border: 1px solid #2a5cb0;
        border-radius: 3px;
      }
      .el-pager li:not(.disabled).active {
        background-color: #4389f9;
      }
      .el-pagination__jump,
      .el-pagination__total {
        color: #fff;
        margin-left: 10px;
      }
    }
  }
}

// 浅色主题
