      }
    }
    .el-form-item__label {
      color: #7f7f7f;
    }
    /*checkbox样式*/
    
    .el-checkbox__input.is-disabled {
      width: 100%;
      padding: 0 auto;
    }
    
    .el-checkbox__input.is-disabled .el-checkbox__inner,
    .el-checkbox__input .el-checkbox__inner {
      background-color: #fff !important;
      width: 16px;
      height: 16px;
      border: 1px solid #bbb;
      border-radius: 2px;
    }
    
    /*解决调整单元格高度后，fixed错位*/
    .el-table__fixed-body-wrapper {
      top: 37px !important;
    }
    
    /*解决表格滚动高度固定导致的问题*/
    .el-table__body-wrapper.is-scrolling-left {
      height: calc(100% - 48px) !important;
    }
    .el-table__body-wrapper.is-scrolling-right {
      height: calc(100% - 48px) !important;
    }
    .el-table__body-wrapper.is-scrolling-middle {
      height: calc(100% - 48px) !important;
    }
    .el-table__fixed-body-wrapper {
      height: calc(100% - 34px) !important;
    }
    
    /*解决调整列宽度时，出现列内容未完整显示的问题*/
    .el-table .cell.el-tooltip {
      width: 100% !important;
    }
    
    .el-table__header-wrapper {
      background-color: #fff!important;
    }
    
    .el-table__body-wrapper {
      background-color: #fff!important;
    }
    
    .el-table__header th {
      // background-color: #22519c !important;
      background: rgba(67,137,249,0.10)!important;
      height: 48px;
      border-color: #bbb;
    
      .cell {
        color: #666 !important;
        text-align: center;
      }
    }
    
    .bda-table-main {
      background-color: #ffffff;
    }
    
    .bda-main {
      /*height: calc(100% - 52px);*/
      border: 1px solid #bbb;
      .el-table__row {
        &:hover > td {
          background-color: #ebfcff !important;
        }
        > td {
          height: 48px;
          border-color: #bbb;
    
          .cell {
            color: #333;
            text-align: center;
            .el-button {
              color: #66b9ff;
            }
          }
        }
        > td:nth-last-child(3) {
          .cell {
            /*color: red;*/
            text-align: center;
          }
        }
      }
      .el-table__row:nth-of-type(odd) {
        background-color: #fff;
      }
      .el-table__row:nth-of-type(even) {
        background-color: #fff;
      }
    }

    // 修改分页组件样式
    .el-pagination {
      margin-top: 20px;
      text-align: center;
      color: #fff;
       .btn-next,
       .btn-prev,
       .el-pager li,
       .el-select .el-input .el-input__inner,
       .el-pagination__editor.el-input .el-input__inner {
         background: rgba(67,137,249,0.06);
         color: #333333;
         border: 0px solid #2a5cb0;
         border-radius: 4px;
       }
       .el-pager li:not(.disabled).active {
         background-color: #4389f9;
       }
      .el-pagination__jump,
      .el-pagination__total {
         color: #fff;
        margin-left: 10px;
      }
    }
  }
}
</style>