<template>
  <div
    :class="$root.themeHomeChange === '1' ? 'project-manage-form_light' : 'project-manage-form'">
    <!--类型-按钮组-->
    <!-- <bda-radio-btn-group
      class="project-manage-radio-group"
      v-model="btnStatus"
      background-color="#4389F9"
    >
      <bda-radio-btn value="实时性检查" label="实时性检查"></bda-radio-btn>
      <bda-radio-btn value="计划性检查" label="计划性检查"></bda-radio-btn>
      <bda-radio-btn value="专项性检查" label="专项性检查"></bda-radio-btn>
    </bda-radio-btn-group> -->

    <!--表单-->
    <el-form
      :model="form"
      :rules="rules"
      ref="form"
      label-width="84px"
      label-suffix="："
    >
      <el-row>
        <!-- <el-col :span="5">
          <bda-button style="background: #4389F9;" @click="addDialogClick">新建合规任务</bda-button>
        </el-col> -->
        <!-- <el-col :span="5">
          <bda-form-item label="所属域" prop="team">
            <bda-select
              v-model="form.team"
              filterable
              clearable
              :searchable="false"
              placeholder="请选择"
              style="width: 100%"
              size="small"
            >
              <bda-option
                v-for="item in teamList"
                :title="item.teamName"
                :key="item.teamId"
                :label="item.teamName"
                :value="item.teamId"
              >
              </bda-option>
            </bda-select>
          </bda-form-item>
        </el-col> -->
        <el-col :span="5">
          <bda-form-item label="任务类型">
            <el-select v-model="form.taskType" clearable placeholder="请选择">
              <el-option
                v-for="item in taskTypeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </bda-form-item>
        </el-col>
        <el-col :span="5">
          <bda-form-item label="检查时间">
            <!-- <el-date-picker
              v-model="checkDate"
              size="mini"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            >
            </el-date-picker> -->
            <bda-date-picker
              v-model="checkDate"
              :clearable="true"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              type="datetimerange"
              value-format="yyyy-MM-dd HH:mm:ss"
              ></bda-date-picker>
          </bda-form-item>
        </el-col>
        <el-col :span="5">
          <bda-form-item label="更新时间">
            <bda-date-picker
              style="width: 100%"
              placeholder="请选择"
              v-model="updateDate"
              value-format="yyyy-MM-dd HH:mm:ss"
              :clearable="true"></bda-date-picker>
          </bda-form-item>
        </el-col>
        <el-col :span="4">
          <bda-search-input
            v-model.lazy="keyword"
            placeholder="请输入关键字"
            style="width: calc(100% - 20px);margin-left:20px"
            @search="searchByKeyVal(keyword)"
            @clear="searchByKeyVal(keyword)"
          >
          </bda-search-input>
        </el-col>
      </el-row>
    </el-form>

    <!-- 弹窗 -->
    <add-dialog @addTableData="addTableData" @closeDialog="initTable(form)" ref="addDialogRef"></add-dialog>
  </div>
</template>
<script>

import addDialog from './dialog/addDialog'
import Interface from "@/base/mixin/risk-control-center.js";

export default {
  components:{addDialog},
  name: "projectManageForm",
  mixins: [Interface],
  data() {
    return {
      btnStatus:'专项性检查', //按钮状态
      checkDate:null,
      updateDate:null,
      
      taskTypeOptions: [
        {
          value:"1",
          label:"实时性检查",
        },
        {
          value:"2",
          label:"计划性检查",
        },
        {
          value:"3",
          label:"专项性检查",
        }
      ],
      
      keyword: "", //关键字
      
      //form
      form: {
        taskType: "",
        // team: "", //所属域
      },

      rules: {},
      // teamList: [], //所属域列表
    };
  },
  
  // 监听form表单
  watch: {
    form: {
      handler(v) {
        this.initTable(v);
      },
      immediate: false,
      deep: true
    },
    // keyword: {
    //   handler(v) {
    //     if(!v) {
    //       this.form['keyword'] = JSON.parse(JSON.stringify(v));
    //     }
    //   },
    //   immediate: false,
    //   deep: true
    // },
    checkDate: {
      handler(v) {
        if(v) {
          this.form.checkStartDate = v[0];
          this.form.checkEndDate = v[1];
        } else {
          this.form.checkEndDate = '';
          this.form.checkStartDate = '';
        }
        this.initTable(this.form);
      },
      deep: true
    },
    updateDate: {
      handler(v) {
        if(v) {
          this.form.updateDate = v;
        } else {
          this.form.updateDate = '';
        }
        this.initTable(this.form);
      },
      deep: true
    }
  },
  
  methods: {
    // 通过key来查询
    searchByKeyVal(v) {
      // Object.defineProperty(this.form, 'keyword', {
      //   value: v
      // });
      this.form['keyword'] = JSON.parse(JSON.stringify(v));

      this.initTable(this.form);
    },
    initTable(params) {
      // window.console.log(params);
      this.$emit('initTable', params);
    },
    goBack() {
      window.history.go(-1);
    },
    addDialogClick() {
      this.$refs.addDialogRef.openDialog();
    },
    
    editDialogClick(data) {
      this.$refs.addDialogRef.editForm(data);
    },
    
    importDocClick() {
      this.$refs.importDocRef.openDialog();
    },

    addTableData(data){
      // this.$emit('addTableData',data)
      this.$refs.addDialogRef.loading = true;
      data.ruleIds = data.ruleIds.map(item => item.id).join(',');
      data.deptId = '暂时无法获取部门ID';
      
      this[data.id ? 'updateRiskTask' : 'addRiskTask'](data).then((rs)=>{
        window.console.log(rs)
        this.$message({
          message: `${data.id ? '修改成功' : '新增成功'}`,
          type: 'success'
        });
        this.$refs.addDialogRef.closeDialog();
        this.$emit('initTable', this.form);
      }).catch(err => {
        this.$message.error(err.code);
        this.$refs.addDialogRef.loading = false;
      }).finally(()=>{})
    }
  },
};
</script>
<style lang="scss" scoped>
.project-manage-form {
  /deep/ {

    /*按钮组样式 */
    .project-manage-radio-group{
      width: 300px;
      background-color: #041338;
      margin-bottom: 20px;

      .bda-radio-btn{
        height: 32px;
        border-color: #4389F9;
        width: 100px;
        box-sizing: border-box;
        padding: 0;

        .bda-radio-btn-text{
          font-family: MicrosoftYaHei;
          font-size: 12px;
          font-weight: 400;
          text-align: center;
          color: #fff;
        }
      }
    }
    .el-button {
      background-color: #041338;
      border: 1px solid #4389f9;
      border-radius: 4px;
      color: #fff;
    }
    
    .bda-date-picker {
      .el-date-editor--datetimerange .el-input__icon,
      .el-input__suffix .el-input__icon {
        // height: calc(100% - 4px);
        background: rgba($color: #041338, $alpha: 1)!important;
        // &:hover {
        //   .el-icon-circle-close {
        //     color: #4298f3;
        //   }
        // }
      }
    }
    
    .el-input__inner {
      background: #041338;
      border: 1px solid #4389f9;
      border-radius: 4px;
      color: #4298f3;
    }
    &.el-input__inner::placeholder {
      color: #4298f3;
    }
    .el-select__caret {
      color: #4298f3;
    }

    .el-form-item__label {
      color: #fff;
    }

    .el-form{
      .el-row{
        height: 32px;
        .el-col{
          height: 32px;

          .el-date-editor{
            width: 100%;

            .el-range-input,.el-input__inner{
              background-color: #041338;
              color: #4298f3;
              &::placeholder{
                color: #4298f3;
              }
            }
            .el-range-separator{
              color: #fff;
            }

          }
        } 
      }
    }
  }
}

// 浅色主题
.project-manage-form_light {
  background-color: rgba($color: #000000, $alpha: 0);
  /deep/ {
    /*按钮组样式 */
    .project-manage-radio-group{
      width: 300px;
      background-color: #041338;
      margin-bottom: 20px;

      .bda-radio-btn{
        height: 32px;
        border-color: #4389F9;
        width: 100px;
        box-sizing: border-box;
        padding: 0;

        .bda-radio-btn-text{
          font-family: MicrosoftYaHei;
          font-size: 12px;
          font-weight: 400;
          text-align: center;
          color: #fff;
        }
      }
    }
    .el-button {
      background-color: #041338;
      border: 1px solid #4389f9;
      border-radius: 4px;
      color: #fff;
    }
    
    .bda-date-picker {
      .el-date-editor--datetimerange .el-input__icon,
      .el-input__suffix .el-input__icon {
        // height: calc(100% - 4px);
        background: rgba($color: #ffffff, $alpha: 1)!important;
        // &:hover {
        //   .el-icon-circle-close {
        //     color: #4298f3;
        //   }
        // }
      }
    }
    
    .el-input__inner {
      background: #fff;
      border: 1px solid #ccc;
      border-radius: 4px;
      color: #666;
    }
    &.el-input__inner::placeholder {
      color: #bbb;
    }
    .el-select__caret {
      color: #4298f3;
    }

    .el-form-item__label {
      color: #333;
    }

    .el-form{
      .el-row{
        height: 32px;
        .el-col{
          height: 32px;

          .el-date-editor{
            width: 100%;

            .el-range-input,.el-input__inner{
              background-color: #fff;
              color: #666;
              &::placeholder{
                color: #bbb;
              }
            }
            .el-range-separator{
              color: #bbb;
            }

          }
        } 
      }
    }
  }
}
</style>