/**
 *  公共API mixin module
 *  @module base/mixin/common
 *  @author coder
 */
import {
  getOrgTreeData
} from '@/base/api/common'

// mixin
export default {

  /**
   * 公共API数据存储载体对象
   * @member common
   * @property {Array} [list=[]] 获取公共API列表响应数据
   * @property {number} [page=null] 获取公共API列表响应页码
   * @property {number} [limit=null] 获取公共API列表页大小，每页记录条数
   * @property {number} [total=0] 获取公共API列表数据记录总条数
   * @property {object} [model=null] 获取公共API单条记录
   */
  data() {
    return {
      common: {
        list: [],
        page: null,
        limit: null,
        total: 0,
        model: null
      }
    }
  },
  methods: {

    /**
     * 获取部门树状数据
     * @function getOrgTreeData
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    getOrgTreeData(data) {
      return getOrgTreeData(data).then(res => {
        return res
      })
    }
  }
}
