<template>
  <bda-dialog
    :class="$root.themeHomeChange === '1' ? 'addDialog_light' : 'addDialog'"
    :visible="dialogVisible"
    :modal="false"
    :close-on-click-modal="false"
    @close="closeDialog"
    @cancel="closeDialog"
    title="新建合规任务"
    v-loading="loading"
    element-loading-text="拼命加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.3)"
  >
    <div class="dialogBox">
      <el-form
        :model="formData"
        :rules="rules"
        ref="form"
        label-width="110px"
        label-suffix="："
        label="任务基础信息"
      >
        <template>
          <p class="tableTitle">任务基础信息</p>
        </template>

        <el-row>
          <el-col :span="8">
            <bda-form-item label="任务名称" prop="taskName">
              <bda-input
                v-model="formData.taskName"
                clearable
                placeholder="请输入"
              ></bda-input>
            </bda-form-item>
          </el-col>
          <el-col :span="8">
            <bda-form-item label="任务类别" prop="taskType">
              <bda-select
                v-model="formData.taskType"
                size="small"
                filterable
                clearable
                placeholder="请选择"
                style="width: 100%"
              >
                <bda-option
                  v-for="item in form.type"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                >
                </bda-option>
              </bda-select>
            </bda-form-item>
          </el-col>
          <el-col :span="8">
            <bda-form-item label="业务流程" prop="processId">
              <el-select
                v-model="formData.processId"
                size="small"
                filterable
                clearable
                placeholder="请选择"
                style="width: 100%"
                @change="processChange"
              >
                <el-option
                  v-for="item in form.process"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                >
                </el-option>
              </el-select>
            </bda-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="8">
            <bda-form-item label="选择项目" prop="projectCode">
              <bda-select
                v-model="formData.projectCode"
                size="small"
                filterable
                clearable
                placeholder="请选择"
                style="width: 100%"
              >
                <bda-option
                  v-for="item in form.projectList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                >
                </bda-option>
              </bda-select>
            </bda-form-item>
          </el-col>
          <el-col :span="8">
            <bda-form-item label="检查单位" prop="checkUnit">
              <bda-input
                v-model="formData.checkUnit"
                clearable
                placeholder="请输入"
              ></bda-input>
            </bda-form-item>
          </el-col>
          <el-col :span="8">
            <bda-form-item label="被检单位" prop="beCheckUnitIds">
              <SelectTree
                style="width: 100%"
                ref="selectTreeRef"
                :props="defaultProps"
                :options="treeData"
                :value="valueId"
                :clearable="isClearable"
                :accordion="isAccordion"
                @getValue="getValue($event)"
              />
            </bda-form-item>
          </el-col>
        </el-row>
        
        <el-row>
          <el-col :span="8">
            <bda-form-item label="检查时间段" prop="checkDate">
              <bda-date-picker
                v-model="formData.checkDate"
                :clearable="false"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                value-format="yyyy-MM-dd HH:mm:ss"
                type="datetimerange"
              ></bda-date-picker>
            </bda-form-item>
          </el-col>
          <el-col :span="8">
            <bda-form-item label="定时任务开始时间" prop="timingTaskStartDate">
              <bda-date-picker
                v-model="formData.timingTaskStartDate"
                :clearable="false"
                placeholder="请选择日期时间"
                value-format="yyyy-MM-dd HH:mm:ss"
                type="datetime"
              ></bda-date-picker>
            </bda-form-item>
          </el-col>
          <el-col :span="8">
            <bda-form-item label="执行周期" prop="taskPerformSync">
              <bda-select
                v-model="formData.taskPerformSync"
                size="small"
                filterable
                clearable
                placeholder="请选择"
                style="width: 100%"
              >
                <bda-option
                  v-for="item in form.taskPerformSync"
                  :key="item.id"
                  :label="item.name"
                  :value="item.value"
                >
                </bda-option>
              </bda-select>
            </bda-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <bda-form-item label="任务简介" prop="taskDesc">
              <bda-input
                type="textarea"
                resize="none"
                v-model="formData.taskDesc"
                clearable
                placeholder="请输入"
              >
              </bda-input>
            </bda-form-item>
          </el-col>
        </el-row>

        <template>
          <p class="tableTitle">检验规则</p>
        </template>

        <el-row>
          <el-col :span="22">
            <bda-form-item prop="taskRules">
              <bda-select
                v-model="formData.taskRules"
                size="small"
                filterable
                clearable
                placeholder="请选择"
                style="width: 260px"
              >
                <bda-option
                  v-for="item in form.taskRules"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                >
                </bda-option>
              </bda-select>
            </bda-form-item>
          </el-col>
          <el-col :span="2" fixed="right">
            <el-button
              style="float: right"
              type="info"
              @click="clearTaskRulesList"
            >清空</el-button>
          </el-col>
        </el-row>

        <div class="taskRulesArea">
          <el-tag
            v-for="tag in formData.ruleIds"
            :key="tag.id"
            closable
            @close="closeTag(tag.id)"
          >
            {{ tag.name }}
          </el-tag>
        </div>
      </el-form>
    </div>
    <span slot="footer" class="dialog-footer">
      <bda-button bdatype="primary" @click="sureDialog">保存</bda-button>
      <el-button type="info" @click="closeDialog">关闭</el-button>
    </span>
  </bda-dialog>
</template>


<script>
import Interface from "@/base/mixin/risk-control-center.js";
import Interface_1 from "@/base/mixin/enterprise-manage-center.js";
import Interface_2 from "@/base/mixin/common.js";
import SelectTree from "@/views/components/treeSelect";
export default {
  mixins: [Interface, Interface_1, Interface_2],
  components: {
    SelectTree,
  },
  data() {
    return {
      dialogVisible: false, //弹框显隐
      form: {
        checkDate: "",
        type: [
          { id: '1', name: "实时性检查" },
          { id: '2', name: "计划性检查" },
          { id: '3', name: "专项性检查" },
        ],
        process: [
          // { id: 1, name: "采购" },
          // { id: 2, name: "合同签订" },
          // { id: 3, name: "行政许可/资格认证" },
          // { id: 4, name: "核算" },
          // { id: 5, name: "验收" },
        ],
        projectList: [],
        taskPerformSync: [
          { id: 0, value: '仅一次', name: "仅一次" },
          { id: 1, value: '每天', name: "每天" },
          { id: 2, value: '每三天', name: "每三天" },
          { id: 3, value: '每一周', name: "每一周" },
          { id: 4, value: '每半个月', name: "每半个月" },
          { id: 5, value: '每一个月', name: "每一个月" },
          { id: 6, value: '每三个月', name: "每三个月" },
          { id: 7, value: '每半年', name: "每半年" },
          { id: 8, value: '每一年', name: "每一年" },
        ],
        taskRules: [
          // { id: 1, name: "出纳与会计稽核/会计制证岗位不能为同一个人" },
          // { id: 2, name: "出纳不得担任对账工作" },
          // {
          //   id: 3,
          //   name: "工作许可人不应签发工作票或担任工作班成员，且与工作负责人不得相互兼任。",
          // },
          // { id: 4, name: "工作票签发人不应兼任该项工作的工作负责人。" },
          // { id: 5, name: "动火工作票签发人不得兼任动火工作负责人。" },
          // {
          //   id: 6,
          //   name: "动火工作票审批人、动火工作监护人不得签发动火工作票。",
          // },
        ],
      },
      rules: {
        taskName: [
          { required: true, message: "请填写文档名称", trigger: "blur" },
        ],
        taskType: [{ required: true, message: "请选择任务类别", trigger: "blur" }],
        checkUnit: [{ required: true, message: "请填写检查单位", trigger: "blur" }],
        checkDate: [
          { required: true, message: "请选择检查时间段", trigger: "blur" },
        ],
        beCheckUnitIds: [
          { required: true, message: "请选择被检单位", trigger: "blur" },
        ],
      },
      
      formData: {
        taskName: "",
        taskType: "",
        project: "",
        projectCode: "",
        checkUnit: "XX检查单位",
        checkDate: "",
        checkStartDate: "",
        checkEndDate: "",
        beCheckUnitIds: "",
        timingTaskStartDate: "",
        taskDesc: "",
        taskRules: "", //显示当前选中标签，以便判断
        ruleIds: [], //已选规则标签
      },

      loading: false,
      
      valueId: [],
      isClearable: true, // 可清空（可选）
      isAccordion: true, // 可回收
      treeData: [],

      defaultProps: {
        children: "list",
        label: "orgName",
        value: "orgId",
      },
    };
  },

  watch: {
    "formData.taskRules": function () {
      if (this.formData.taskRules != "") {
        let current = this.form.taskRules.find(item => { return item.id === this.formData.taskRules });
        if (this.formData.ruleIds.length === 0) {
          const tag = { id: current.id, name: current.name };
          this.formData.ruleIds.push(tag);
        } else {
          if (
            this.formData.ruleIds.some(
              (el) => this.formData.taskRules === el.id
            )
          ) {
            return;
          } else {
            const tag = { id: current.id, name: current.name };
            this.formData.ruleIds.push(tag);
          }
        }
      } else {
        return;
      }
    },
    "formData.checkDate" : {
      handler(v) {
        if(v) {
          this.formData.checkStartDate = v[0];
          this.formData.checkEndDate = v[1];
        } else {
          this.formData.checkStartDate = "";
          this.formData.checkEndDate = "";
        }
      },
      deep: true
    },
    "formData.projectCode" : {
      handler(v) {
        if(v) {
          let row = this.form.projectList.find(item => item.id === v)
          this.formData.project = (row && row.name) || '';
        } else {
          this.formData.project = "";
        }
      },
      deep: true
    }
  },
  computed: {
    /* 转树形数据 */
    optionData() {
      let cloneData = JSON.parse(JSON.stringify(this.treeData)); // 对源数据深度克隆
      return cloneData.filter((father) => {
        // 循环所有项，并添加children属性
        let branchArr = cloneData.filter((child) => father.id == child.id); // 返回每一项的子级数组
        branchArr.length > 0 ? (father.children = branchArr) : ""; //给父级添加一个children属性，并赋值
        return father.id == 0; //返回第一层
      });
    },
  },
  
  mounted() {},
  
  methods: {
    getOrgData() {
      this.getOrgTreeData().then((rs)=>{
        // window.console.log(rs)
        if(Array.isArray(rs) && rs.length !== 0) {
          // window.console.log(rs)
          this.treeData = rs;
        } else {
          this.treeData = [];
        }
      }).finally(()=>{});
    },
    getProject_list() {
      this.getProjectList().then((rs)=>{
        // window.console.log(rs)
        if(Array.isArray(rs) && rs.length !== 0) {
          // window.console.log(rs)
          this.form.projectList = rs.map(item => { return { id: item, name: item} });
        } else {
          this.form.projectList = [];
        }
      }).finally(()=>{});
    },
    /**
     * @description:打开弹框回调
     */
    openDialog() {
      this.initForm();
      this.processChange('');
      this.dialogVisible = true;
      this.formData = {
        taskName: "",
        taskType: "",
        project: "",
        projectCode: "",
        checkUnit: "XX检查单位",
        checkDate: "",
        checkStartDate: "",
        checkEndDate: "",
        beCheckUnitIds: "",
        timingTaskStartDate: "",
        taskDesc: "",
        taskRules: "", //显示当前选中标签，以便判断
        ruleIds: [], //已选规则标签
      };
    },

    /**
     * @description:关闭弹框回调
     */
    closeDialog() {
      this.dialogVisible = false;
      this.loading = false;
      this.$refs['form'].resetFields();
      this.valueId = [];
      this.$refs.selectTreeRef.clearHandle();
      // this.$emit('closeDialog');
    },

    /**
     * @description:确定弹框回调
     */
    sureDialog() {
      // window.console.log(this.formData)
      this.$refs.form.validate((valid) => {
        if (valid) {
          // this.addTableData();
          this.$emit("addTableData", JSON.parse(JSON.stringify(this.formData)));
        }
      });
    },
    
    // 获取校验规则通过业务流程
    processChange(processId) {
      let pIdRow = this.form.process.find(item => item.id == processId);
      this.formData.businessProcessId = (pIdRow && pIdRow.pId) || '';
      this.getTaskProcess({ywlcId: processId}).then((rs)=>{
        window.console.log(rs)
        if(Array.isArray(rs) && rs.length !== 0 ) {
          this.form.taskRules = rs.map(item => {
            return { id: item.code, name: item.name };
          })
        } else {
          this.form.taskRules = [];
        }
      }).finally(()=>{})
    },
    
    // 初始化表单
    initForm() {
      this.getOrgData();
      this.getProject_list();
      this.getProcessList({usePage: false}).then((rs)=>{
        // window.console.log(rs)
        if(rs) {
          this.form.process = rs.map(item => { return {id: item.ywlcId, pId: item.id, name: item.ywlcName}});
        } else {
          this.form.process = [];
        }
      }).finally(()=>{});
    },
    
    addTableData() {
      this.$emit("addTableData", this.formData);
      // this.dialogVisible = false;
    },
    
    editForm(data) {
      // window.console.log(data);
      this.initForm();
      this.dialogVisible = true;
      this.formData.checkDate = [data.checkStartDate, data.checkEndDate];
      // if(!(Array.isArray(data.ruleIds))) {
      //   data.ruleIds = data.ruleIds ? data.ruleIds.split(',').map(id => {
      //     return this.form.taskRules.find(item => {
      //       return item.id == id
      //     })
      //   }) : [];
      // }
      // this.valueId = data.beCheckUnitIds ? data.beCheckUnitIds.split(',') : [];
      // this.$refs.selectTreeRef.selectData
      Object.keys(data).forEach(key => {
        this.formData[key] = data[key];
      })
      data.processId && this.processChange(data.processId);
      
      data.id && this.getRuleListById(data.id);
      
      data.id && this.getBeCheckUnitByTaskId(data.id);
    },
    
    // 根据任务id查询被检查单位
    getBeCheckUnitByTaskId(id) {
      this.getBeCheckUnitById({taskId: id}).then((rs)=>{
        // window.console.log(rs)
        if(Array.isArray(rs) && rs.length !== 0 ) {
          this.valueId = rs;
          this.getValue(rs);
        } else {
          this.valueId = [];
        }
      }).finally(()=>{})
    },
    
    // 根据任务id查询校验规则
    getRuleListById(id) {
      this.getRuleTableListByTaskId({taskId: id}).then((rs)=>{
        
        if(Array.isArray(rs) && rs.length !== 0 ) {
          this.formData.ruleIds = rs.map(item => {
            return { id: item.id, name: item.ruleName };
          });
          // window.console.log(rs, this.form.ruleIds)
        } else {
          this.formData.ruleIds = [];
        }
      }).finally(()=>{})
    },
    
    closeTag(id) {
      this.formData.ruleIds = this.formData.ruleIds.filter(
        (tag) => tag.id !== id
      );
    },
    clearTaskRulesList() {
      this.formData.ruleIds = [];
    },

    // 取值
    getValue(v) {
      if(Array.isArray(v) && v.length !== 0) {
        this.formData.beCheckUnitIds = v.map(item => item[this.defaultProps.value]).join(',');
      } else {
        this.formData.beCheckUnitIds = "";
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.addDialog {
  /deep/ {
    /*修改添加规则弹窗 */
    .el-dialog {
      width: 1094px;
      height: 624px;
      background: #1f2c4d;
      border: 1px solid #4389f9;
      border-radius: 4px;
      position: relative;
      z-index: 19999;
      
      .el-dialog__header {
        border-bottom: 1px solid #4389f9 !important;
        height: 48px !important;
  
        .bda-dialog-header .bda-dialog-header-title {
          font-family: MicrosoftYaHei-Bold;
          font-size: 16px !important;
          color: #ffffff !important;
          line-height: 48px;
          font-weight: 700;
        }
      }
  
      .bda-dialog-body-main {
        height: 522px !important;
        .dialogBox {
          height: 100%;
          .el-form {
            .tableTitle {
              height: 21px;
              font-family: MicrosoftYaHei-Bold;
              font-size: 16px;
              color: #ffffff;
              font-weight: 700;
              margin-bottom: 20px;
            }
          
            .el-row {
              margin-bottom: 20px;
              .bda-date-picker {
                width: 100%;
              }
            }
          
            .el-row:nth-of-type(4) {
              height: 80px;
              .el-textarea__inner {
                height: 80px;
                background: #041338;
                border: 1px solid #4389f9;
                color: #4298f3;
                &::placeholder {
                  font-family: MicrosoftYaHei;
                  font-size: 14px;
                  color: #4298f3;
                  font-weight: 400;
                }
              }
            }
            .el-row:nth-of-type(5) {
              margin-bottom: 10px;
              .el-form-item {
                .el-form-item__content {
                  margin-left: 0 !important;
                }
              }
            }
          
            .taskRulesArea {
              width: 100%;
              height: 94px;
              overflow-y: auto;
              padding: 10px;
              background: #041338;
              border: 1px solid #4389f9;
              border-radius: 4px;
              box-sizing: border-box;
          
              .el-tag {
                height: 32px;
                margin-right: 10px;
                margin-bottom: 10px;
                background: #0d3980;
                border: none;
                border-radius: 4px;
                font-family: MicrosoftYaHei;
                font-size: 14px;
                color: #ffffff;
                text-align: center;
                font-weight: 400;
                position: relative;
          
                .el-tag__close.el-icon-close {
                  color: #4389f9;
                  background: #041338;
                  border: 1px solid #4389f9;
                  position: absolute;
                  top: -8px;
                  right: -8px;
                }
              }
            }
          }
        }
      }
  
      .el-dialog__footer {
        border-top: 1px solid #4389f9 !important;
        height: 52px !important;
  
        .dialog-footer {
          display: block;
          height: 100%;
          padding: 11px 20px;
          box-sizing: border-box;
        }
      }
    }
  }
}
// 浅色主题 
.addDialog_light {
  /deep/ {
    .el-button--info {
      background: #fff!important;
      border: 1px solid #bbb!important;
      color: #333!important;
    }
    
    /*修改添加规则弹窗 */
    .el-dialog {
      width: 1094px;
      height: 624px;
      background: #fff;
      border: 1px solid #bbb;
      border-radius: 4px;
      position: relative;
      z-index: 19999;
  
      .el-dialog__header {
        border-bottom: 1px solid #bbb !important;
        height: 48px !important;
  
        .bda-dialog-header .bda-dialog-header-title {
          font-family: MicrosoftYaHei-Bold;
          font-size: 16px !important;
          color: #333 !important;
          line-height: 48px;
          font-weight: 700;
        }
      }
  
      .bda-dialog-body-main {
        height: 522px !important;
        .dialogBox {
          height: 100%;
          .el-form {
            .tableTitle {
              height: 21px;
              font-family: MicrosoftYaHei-Bold;
              font-size: 16px;
              color: #333;
              font-weight: 700;
              margin-bottom: 20px;
            }
          
            .el-row {
              margin-bottom: 20px;
              .bda-date-picker {
                width: 100%;
              }
            }
          
            .el-row:nth-of-type(4) {
              height: 80px;
              .el-textarea__inner {
                height: 80px;
                background: #fff;
                border: 1px solid #bbb;
                color: #333;
                &::placeholder {
                  font-family: MicrosoftYaHei;
                  font-size: 14px;
                  color: #bbb;
                  font-weight: 400;
                }
              }
            }
            .el-row:nth-of-type(5) {
              margin-bottom: 10px;
              .el-form-item {
                .el-form-item__content {
                  margin-left: 0 !important;
                }
              }
            }
          
            .taskRulesArea {
              width: 100%;
              height: 94px;
              overflow-y: auto;
              padding: 10px;
              background: #fff;
              border: 1px solid #bbb;
              border-radius: 4px;
              box-sizing: border-box;
          
              .el-tag {
                height: 32px;
                margin-right: 10px;
                margin-bottom: 10px;
                background: rgba(67,137,249,0.16);
                border: none;
                border-radius: 4px;
                font-family: MicrosoftYaHei;
                font-size: 14px;
                color: #4389F9;
                text-align: center;
                font-weight: 400;
                position: relative;
          
